.wrapper {
    min-height: 200px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,.3)),to(rgba(255,255,255,0.7))),url('/assets/images/skyline.jpg');
    background-image: -webkit-linear-gradient(top, rgba(255,255,255,.3) 0%,rgba(255,255,255,0.7) 100%),url('/assets/images/skyline.jpg');
    background-image: -o-linear-gradient(top, rgba(255,255,255,.3) 0%,rgba(255,255,255,0.7) 100%),url('/assets/images/skyline.jpg');
    background-image: linear-gradient(to bottom, rgba(255,255,255,.3) 0%,rgba(255,255,255,0.7) 100%),url('/assets/images/skyline.jpg');
    background-repeat: repeat-x;
}


